


//----- Extends
%clearfix:after {
    display:block;
    clear:both;
    content:'';
}
 

 
//----- Grid System
$grid-spacing:3%;
 
.row {
    @extend %clearfix;
 
    & + & {
        margin-top:$grid-spacing;
    }
}
 
[class^="col-"] {
    float:left;
    margin-right:$grid-spacing;
    box-sizing:border-box;
 
    &:last-child {
        margin-right:0%;
    }
}
 


//----- Columns (Manual)
